import React from "react";
import { Link, Navigate } from 'react-router-dom';

import logo from "../assets/images/common/logo.png";
import close_btn from "../assets/images/sub/close_btn.png";
import list_icon from "../assets/images/sub/list_icon.png";


const NoticeView = props => {
    console.log(props.authenticated);

    if (!props.authenticated) return <Navigate to={"/"} />;

    return (
        <div id="sub">
            <div className="wrap">
                <div className="top">
                    <h1><Link to="/"><img src={logo} alt=""/></Link></h1>
                    <div className="close"><Link to="/"><img src={close_btn} alt="Close"/></Link></div>
                </div>
                <ul className="tap">
                <li><Link to="/deposit"></Link></li>
                    <li><Link to="/withdraw"></Link></li>
                    <li><Link to="/betHistory"></Link></li>
                    <li><Link to="/payHistory"></Link></li>
                    <li><Link to="/qnaHistory"></Link></li>
                    <li><Link to="/notice" className="on"></Link></li>
                </ul>
                <div className="content">
                    <table className="board-list">
                        <colgroup>
                        <col style={{width:"12%"}} />
                            <col  />
                            <col style={{width:"18%"}} />
                        </colgroup>
                        <thead>
                            <tr>
                                <th></th>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>10</td>
                                <td className="subject"><Link to="/notice"> .</Link></td>
                                <td>2022.11.11</td>
                            </tr>
                        </tbody>
                    </table>
                    <div className="view">
                         .  .  .  . <br/>
                         .  .  .  . <br/>
                         .  .  .  . <br/>
                         .  .  .  . <br/>
                         .  .  .  . 
                    </div>
                    <div className="button"><Link to="/notice"><img src={list_icon} alt=""/></Link></div>
                </div>
            </div>
        </div>
    );
}

export default NoticeView;
