import React, { useState } from "react";
import { Link, Navigate } from 'react-router-dom';
import axios from 'axios';

import logo from "../assets/images/common/logo.png";
import close_btn from "../assets/images/sub/close_btn.png";
import add_icon from "../assets/images/sub/add_icon.png";

import { bankList } from "../const";


const Join = props => {

    const [userId, setUserId] = useState("");
    const [password, setPassword] = useState("");
    const [repassword, setRePassword] = useState("");
    const [nick, setNick] = useState("");
    const [year, setYear] = useState("");
    const [month, setMonth] = useState("");
    const [day, setDay] = useState("");
    const [tel1, setTel1] = useState("");
    const [tel2, setTel2] = useState("");
    const [tel3, setTel3] = useState("");
    const [bank, setBank] = useState("");
    const [bankHolder, setBankHolder] = useState("");
    const [bankAccount, setBankAccount] = useState("");
    const [recCode, setRecCode] = useState("");

    const [modalShow, setModalShow] = useState(false);
    const [modalDisplay, setModalDisplay] = useState('none');
    const [modalTitle, setModalTitle] = useState("");
    const [modalContent, setModalContent] = useState("");
    console.log(props.authenticated);

    if (props.authenticated) return <Navigate to={"/"} />;

    async function RegisterUser(userInfo) {
        return axios("/reqRegister", {
            method:"POST",
            headers: {
                'Content-Type': 'application/json'
            },
            data: userInfo
        })
        .then(function(res){
            if(res.data.statusCode == 200){
                showPopup(' ', '   .');
            } else if(res.data.statusCode == 204) {
                showPopup('  ', '  .');
            }
        });
    }

    async function checkId(userid) {
        return axios("/checkUserId", {
            method:"POST",
            headers: {
                'Content-Type': 'application/json'
            },
            data: userid
        })
        .then(res=>res.data)
    }

    function JoinPopup(props) {
        return (
            <div id="popup2" style={{display: props.display}}>
              <div className="box">
                <h1> {props.title} </h1>
                <p  className="txt"> {props.content} </p>
                <div className="ok"><a href="#" onClick={props.onHide}></a></div>
              </div>
            </div>
        );
    }

    function showPopup(title, content) {
        setModalTitle(title);
        setModalContent(content);
        setModalShow(true);
        setModalDisplay('block');
    }

    function closePopup() {
        setModalShow(false);
        setModalDisplay('none');
    }

    const reqRegister = async e => {
        e.preventDefault();

        if(password != repassword) {
            showPopup(' ', '  .');
        }
        if(userId == "" || password == "" || nick == "" || year == "" ||
         month == "" || day == "" || tel1 == "" || tel2 == "" || tel3 == "" ||
         bank == "" || bankHolder == "" || bankAccount == "" || recCode == ""
        ) {
            showPopup(' ', '     .');
        }
        const token = await RegisterUser({
            userId,
            password,
            nick,
            year,
            month,
            day,
            tel1,
            tel2,
            tel3,
            bank,
            bankHolder,
            bankAccount,
            recCode,

        });
    }

    const checkUserId = async e => {
        e.preventDefault();
        if(userId == "") {
            showPopup(' ', '  ');
        } else {
            const response = await checkId({userId});
            if(response.statusCode == 200) {
                showPopup(' ', ' ');
            } else {
                showPopup(' ', ' ');
            }
        }
    }

    return (
        <>
        <JoinPopup
            show={modalShow}
            display={modalDisplay}
            title={modalTitle}
            content={modalContent}
            onHide={() => closePopup()}
        />
        <div id="sub">
            <div className="wrap">
                <div className="top">
                    <h1><Link to="/"><img src={logo} alt=""/></Link></h1>
                    <div className="close"><Link to="/"><img src={close_btn} alt="Close"/></Link></div>
                </div>
                <ul className="tap">
                    <li className="member"><Link to="/login"></Link></li>
                    <li className="member"><Link to="/join" className="on"></Link></li>
                </ul>
                <div className="content">
                    <table className="board-write2">
                        <tbody>
                            <tr>
                                <th></th>
                                <td><input type="text" value={nick} onChange={(e) => setNick(e.target.value)} placeholder="" /></td>
                                <th></th>
                                <td>
                                    <div className="check">
                                        <input type="text" value={userId} onChange={(e) => setUserId(e.target.value)} placeholder="" />
                                        <a href="#" onClick={checkUserId}></a>
                                    </div>
                                    <p className="txt"> ,     3  .</p>
                                </td>
                            </tr>
                            <tr>
                                <th></th>
                                <td>
                                    <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} placeholder="" />
                                    <p className="txt">   8  ++  .</p>
                                </td>
                                <th> </th>
                                <td>
                                    <input type="password" value={repassword} onChange={(e) => setRePassword(e.target.value)} placeholder=" " />
                                    <p className="txt">   .</p>
                                </td>
                            </tr>
                            <tr>
                                <th></th>
                                <td>
                                    <input type="number" value={year} onChange={(e) => setYear(e.target.value)} className="tel" placeholder="" min="1900" max="2100"/>
                                    <input type="number" value={month} onChange={(e) => setMonth(e.target.value)} className="tel" placeholder="" min="1" max="12" />
                                    <input type="number" value={day} onChange={(e) => setDay(e.target.value)} className="tel" placeholder="" min="1" max="31"/>
                                </td>
                                <th></th>
                                <td>
                                    <input type="tel" value={tel1} onChange={(e) => setTel1(e.target.value)} className="tel" />
                                    <input type="tel" value={tel2} onChange={(e) => setTel2(e.target.value)} className="tel" />
                                    <input type="tel" value={tel3} onChange={(e) => setTel3(e.target.value)} className="tel" />
                                </td>
                            </tr>
                            <tr>
                                <th></th>
                                <td>
                                    <select value={bank} onChange={(e) => setBank(e.target.value)}>
                                        <option>---  ---</option>
                                        {bankList.map(({ value, label }, index) => <option value={value} key={value} >{label}</option>)}
                                    </select>
                                </td>
                                <th></th>
                                <td><input type="text" value={bankHolder} onChange={(e) => setBankHolder(e.target.value)} placeholder="" /></td>
                            </tr>
                            <tr>
                                <th></th>
                                <td><input type="text" value={bankAccount} onChange={(e) => setBankAccount(e.target.value)} placeholder="" /></td>
                                <th></th>
                                <td>
                                    <input type="text" value={recCode} onChange={(e) => setRecCode(e.target.value)} placeholder="" />
                                    <p className="txt">    .</p>
                                </td>
                            </tr>
                        </tbody>
                        
                    </table>
                    <div className="button"><a href="#" className="on" onClick={reqRegister}><img src={add_icon} alt=""/></a></div>
                </div>
            </div>
        </div>
        </>
    );
}

export default Join;
