import React from "react";
import { Link, Navigate, useLocation, useNavigate } from 'react-router-dom';

import logo from "../assets/images/common/logo.png";
import ok_icon from "../assets/images/sub/ok_icon.png";
import close_btn from "../assets/images/sub/close_btn.png";
import warning_img from "../assets/images/main/warning_img.png";
import cancel_icon from "../assets/images/sub/cancel_icon.png";


const EnterGameWarning = props => {
    console.log(props.authenticated);
    const location = useLocation();
    let navigate = useNavigate();
    let gameLink = '';
    try {
        gameLink = location.state.url;
    } catch (error) {
        if (!props.authenticated) return <Navigate to={"/"} />;
    }

    function openGame() {
        // window.open(gameLink, "", "_blank");
        navigate("/");
        window.open(gameLink);
    }
    

    return (
        <div id="sub">
            <div className="wrap">
                <div className="top">
                    <h1><a href="/"><img src={logo} alt=""/></a></h1>
                    <div className="close"><Link to="/"><img src={close_btn} alt="Close"/></Link></div>
                </div>
                <div className="content">
                    <div className="warning">
                        <p><img src={warning_img} alt=""/></p>
                        <em>          , </em>
                        <em>       . </em>
                        <em>    <span>" "</span>     .</em>
                    </div>
                    <div className="button">
                        <a href="#" className="on" onClick={openGame}><img src={ok_icon} alt=""/></a>
                        <Link to="/"><img src={cancel_icon} alt=""/></Link>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default EnterGameWarning;
