import React, { useState } from "react";
import { Link, useNavigate, Navigate } from 'react-router-dom';
// import { setCookie, getCookie } from '../action/cookie';
import { setCookie } from '../action/cookie';
import axios from 'axios';

import logo from "../assets/images/common/logo.png";
import close_btn from "../assets/images/sub/close_btn.png";
import ok_icon from "../assets/images/sub/ok_icon.png";
import add_icon from "../assets/images/sub/add_icon.png";




async function loginUser(userInfo) {
    console.log(userInfo);
    
    return axios("/reqLogin", {
        method:"POST",
        headers: {
            'Content-Type': 'application/json'
        },
        data: userInfo
    })
    .then(res=>res.data);
}

const Login = (props) => {
    console.log(props);
    const [userId, setUserId] = useState("");
    const [password, setPassword] = useState("");
    const [modalShow, setModalShow] = useState(false);
    const [modalDisplay, setModalDisplay] = useState('none');
    const [modalTitle, setModalTitle] = useState("");
    const [modalContent, setModalContent] = useState("");

    let navigate = useNavigate();
    if (props.authenticated) return <Navigate to={"/"} />;

    function LoginPopup(props) {
        return (
            <div id="popup2" style={{display: props.display}}>
              <div className="box">
                <h1> {props.title} </h1>
                <p  className="txt"> {props.content} </p>
                <div className="ok"><a href="#" onClick={props.onHide}></a></div>
              </div>
            </div>
        );
    }

    function showPopup(title, content) {
        setModalTitle(title);
        setModalContent(content);
        setModalShow(true);
        setModalDisplay('block');
    }

    function closePopup() {
        setModalShow(false);
        setModalDisplay('none');
    }

    const reqLogin = async e => {
        e.preventDefault();
        if(userId == "" || password == "") {
            showPopup(' ', '     .');
        } else {
            const response = await loginUser({
                userId,
                password
            });
            // setToken(token);
            console.log("response: ", response);
            if(response.statusCode != 200) {
                showPopup(' ', response.error);
            } else {
                var userInfo = response.data;
                console.log(userInfo);
    
                for (const[key, value] of Object.entries(userInfo))
                {
                    console.log('key: ', [key]);
                    console.log('value: ', [value]);

                    setCookie([key], [value]);
                    // window.sessionStorage.setItem(key, value);
                }
                setCookie(['authenticated'], [true]);
                window.location.reload();
            }
        }
    }


    return (
        <>
        <LoginPopup
            show={modalShow}
            display={modalDisplay}
            title={modalTitle}
            content={modalContent}
            onHide={() => closePopup()}
        />
        <div id="sub">
            <div className="wrap">
                <div className="top">
                    <h1><Link to="/"><img src={logo} alt=""/></Link></h1>
                    <div className="close"><Link to="/"><img src={close_btn} alt="Close"/></Link></div>
                </div>
                <ul className="tap">
                    <li className="member"><Link to="/login" className="on"></Link></li>
                    <li className="member"><Link to="/join"></Link></li>
                </ul>
                <div className="content">
                    <table className="board-write">
                        <tbody>
                            <tr>
                                <th></th>
                                <td><input type="text" value={userId} onChange={(e) => setUserId(e.target.value)} placeholder="" /></td>
                            </tr>
                            <tr>
                                <th></th>
                                <td><input type="text" value={password} onChange={(e) => setPassword(e.target.value)} placeholder="" /></td>
                            </tr>
                        </tbody>
                    </table>
                    <div className="button">
                        <a href="#" className="on" onClick={reqLogin}><img src={ok_icon} alt=""/></a>
                        <Link to="/join"><img src={add_icon} alt=""/></Link>
                    </div>
                </div>
            </div>
        </div>
        </>
    );
}

export default Login;