import React, { useEffect, useState } from "react";
import { Link, Navigate } from 'react-router-dom';
import { getCookie } from '../action/cookie';
import axios from 'axios';

import logo from "../assets/images/common/logo.png";
import close_btn from "../assets/images/sub/close_btn.png";
import ok_icon from "../assets/images/sub/ok_icon.png";

const Deposit = props => {

    const [userId, setUserId] = useState("");
    const [depositMoney, setDepositMoney] = useState("");
    const [nick, setNick] = useState("");
    const [bankHolder, setBankHolder] = useState("");
    const [phoneNumber, setPhoneNumber] = useState("");
    const [coupon, setCoupon] = useState("");

    const [modalShow, setModalShow] = useState(false);
    const [modalDisplay, setModalDisplay] = useState('none');
    const [modalTitle, setModalTitle] = useState("");
    const [modalContent, setModalContent] = useState("");


    useEffect(() => {
        console.log(window.sessionStorage);
        setUserId(getCookie(["userid"]));
        setNick(getCookie(["nick"]));
        setBankHolder(getCookie(["bankHolder"]));
        setPhoneNumber(getCookie(["phoneNumber"]));
    }, []);
    console.log(props.authenticated)
    if (!props.authenticated) return <Navigate to={"/"} />;

    function DepositPopup(props) {
        return (
            <div id="popup2" style={{display: props.display}}>
              <div className="box">
                <h1> {props.title} </h1>
                <p  className="txt"> {props.content} </p>
                <div className="ok"><a href="#" onClick={props.onHide}></a></div>
              </div>
            </div>
        );
    }

    function showPopup(title, content) {
        setModalTitle(title);
        setModalContent(content);
        setModalShow(true);
        setModalDisplay('block');
    }

    function closePopup() {
        setModalShow(false);
        setModalDisplay('none');
    }


    async function reqDeposit () {
        if(depositMoney == 0) {
            showPopup(' ', '  ');
        } else {
            console.log(depositMoney);
            console.log(nick);
            console.log(bankHolder);
            console.log(phoneNumber);
            axios("/users/reqDeposit", {
                method:"POST",
                headers: {
                    'Content-Type': 'application/json'
                },
                data: {
                    userid: userId,
                    depositMoney: depositMoney,
                }
            })
            .then(function(res){
                console.log(res.data)
                if(res.data.statusCode == 200){
                    showPopup(' ', ' .');
                } else {
                    showPopup('  ', ' .    .');
                }
            });
        }
    };

    async function reqBankAccount() {
        if(userId == null) {
            showPopup(' ', '  .');
        } else {
            axios("/users/reqBankAccount", {
                method:"POST",
                headers: {
                    'Content-Type': 'application/json'
                },
                data: {userid: userId}
            })
            .then(function(res){
                if(res.data.statusCode == 200){
                    showPopup(' ', '  .');
                } else {
                    showPopup('  ', '  .    .');
                }
            });
        }
    }

    
    return (
        <>
        <DepositPopup
            show={modalShow}
            display={modalDisplay}
            title={modalTitle}
            content={modalContent}
            onHide={() => closePopup()}
        />
        <div id="sub">
            <div className="wrap">
                <div className="top">
                    <h1><Link to="/"><img src={logo} alt=""/></Link></h1>
                    <div className="close"><Link to="/"><img src={close_btn} alt="Close"/></Link></div>
                </div>
                <ul className="tap">
                    <li><Link to="/deposit" className="on"></Link></li>
                    <li><Link to="/withdraw"></Link></li>
                    <li><Link to="/betHistory"></Link></li>
                    <li><Link to="/payHistory"></Link></li>
                    <li><Link to="/qnaHistory"></Link></li>
                    <li><Link to="/notice"></Link></li>
                </ul>
                <div className="content">
                    <div class="inquiry"><a href="#" onClick={reqBankAccount}></a>    !</div>
                    <table className="board-write">
                        <tbody>
                            <tr>
                                <th></th>
                                <td>
                                    <input type="text" value={depositMoney.toLocaleString('ko-KR')} onChange={(e) => setDepositMoney(e.target.value)} placeholder=" 10,000" />
                                    <ul className="amount">
                                        <li><a href="#" onClick={(e) => setDepositMoney(10000)}></a></li>
                                        <li><a href="#" onClick={(e) => setDepositMoney(50000)}></a></li>
                                        <li><a href="#" onClick={(e) => setDepositMoney(100000)}></a></li>
                                        <li><a href="#" onClick={(e) => setDepositMoney(500000)}></a></li>
                                        <li><a href="#" onClick={(e) => setDepositMoney(1000000)}></a></li>
                                        <li><a href="#" onClick={(e) => setDepositMoney("")} className="reset" ></a></li>
                                    </ul>
                                </td>
                            </tr>
                            <tr>
                                <th></th>
                                <td><input type="text" value={nick} disabled={true} placeholder="" /></td>
                            </tr>
                            <tr>
                                <th></th>
                                <td>
                                    <input type="text" value={bankHolder} disabled={true} placeholder="" />
                                    <p className="care">         .</p>
                                </td>
                            </tr>
                            <tr>
                                <th></th>
                                <td><input type="text" value={phoneNumber} disabled={true} placeholder="01000000000" /></td>
                            </tr>
                            <tr>
                                <th></th>
                                <td>
                                    <select value={coupon} onChange={(e) => setCoupon(e.target.value)}>
                                        <option></option>
                                    </select>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <div className="button"><a href="#" className="on" onClick={reqDeposit}><img src={ok_icon} alt=""/></a></div>
                </div>
            </div>
        </div>
        </>
    );
}

export default Deposit;
